const ADD_GUN = '';
const REMOVE_GUN = '';

//reducer
export function counter(state = 0, action) {
    switch (action.type) {
        case ADD_GUN:
            return state + 1;
        case REMOVE_GUN:
            return state - 1;
        default:
            return 10;
    }
}


//action creator
export function addGUN() {
    return { type: ADD_GUN }
}

export function removeGUN() {
    return { type: REMOVE_GUN }
}



export function addGunAsync(){
    return dispatch=>{
        setTimeout(()=>{
            dispatch(addGUN())
        },2000)
    }
}


